import {CONST_RAKUTEN_WEBSEARCH} from  '../../../utilities/src/setting_config'
import {validateRuntimeOnUserInteraction} from '../../../utilities/src/runtime_helper';

var searchbar = document.getElementById('box');

var buttons = searchbar.getElementsByTagName("button");
buttons[0].addEventListener("click", function() {
  validateRuntimeOnUserInteraction(() => {
    hideToolbar();
  });
});
buttons[1].addEventListener("click", function() {
  validateRuntimeOnUserInteraction(() => {
    platform_api_root.runtime.sendMessage({
        method: "createTab",
        url: `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/Web?qt=` +  encodeURIComponent(box.value) + '&ref=platform_specific_ref&tool_id=1'
    });
  });
});
buttons[2].addEventListener("click", function() {
  validateRuntimeOnUserInteraction(() => {
    platform_api_root.runtime.sendMessage({
        method: "createTab",
        url: !box.value.trim().length ? `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=ichiba&platform=unifiedext` :
          `https://websearch.rakuten.co.jp/Shopping?query=${encodeURIComponent(box.value)}&ref=chexti&tool_id=1&col=OW`
    });
  });
});
buttons[3].addEventListener("click", function() {
  validateRuntimeOnUserInteraction(() => {
    platform_api_root.runtime.sendMessage({
        method: "createTab",
        url: '/options.html'
    });
  });
});

var box = document.getElementById('searchBox');
box.addEventListener("keydown", function(event) {
  validateRuntimeOnUserInteraction();
  switch (event.keyCode) {
    case 13: //ENTER key.
      event.preventDefault();
      shrinkToolbar();
      break;
    case 27: //ESCAPE key.
      hideToolbar();
      break;
    default:
      expandToolbar();
  }
});
box.addEventListener("focusout", function(event) {
  shrinkToolbar();
});
box.addEventListener("input", function(event) {
  notifyBoxValueChange(box.value);
});

function shrinkToolbar() {
  window.parent.postMessage({method:"RTBcs.ToolbarManager.shrink"}, '*');
}

function expandToolbar() {
  window.parent.postMessage({method:"RTBcs.ToolbarManager.expand"}, '*');
}

function hideToolbar() {
  window.parent.postMessage({method:"RTBcs.ToolbarManager.hide"}, '*');
}

function notifyBoxValueChange() {
  window.parent.postMessage({method:"RTBcs.ToolbarManager.setBoxValue", value:box.value}, '*');
}

window.addEventListener('message', function(e) {
  var msg = e.data;
  if (msg.method == 'setBoxValue') {
    box.value = msg.value;
  }
});

window.addEventListener('load', function(e) {
  window.parent.postMessage({method:"RTBcs.ToolbarManager.onload"}, '*');
});
