export function checkRuntimeValid(callback) {
    // In Chrome and Firefox `runtime.sendMessage` throws an error if the
    // extension is disabled.
    // In Old Edge it performs as normal but the response is `undefined` always.
    try {
        platform_api_root.runtime.sendMessage({
            method: "checkBackgroundAlive"
        }, (response) => {
            const alive = !platform_api_root.runtime.lastError &&
                          response === true;
            callback(alive);
        });
    } catch(e) {
        callback(false);
    }
}

export function validateRuntimeOnUserInteraction(callback) {
    checkRuntimeValid((alive) => {
        if (alive) {
            if (callback) {
                callback();
            }
        } else {
            window.alert('Please enable RakutenToolbar extension.');
        }
    });
}
